.main {
  display: flex;
  justify-content: center;
  padding-top: 88px;
}
@media (min-width: 1025px) {
  .header .ant-menu,
  .header .header-content .othersites {
    display: none;
  }
}
@media (max-width: 1024px) {
  .ant-layout-sider {
    display: none;
  }
  .content-layout {
    margin: 0;
  }
  .ant-breadcrumb {
    display: none;
  }
}
@media (max-width: 768px) {
  .header {
    padding: 0;
  }
  .header .header-content .othersites {
    right: 32px;
  }
}
@media (max-width: 576px) {
  .header .header-content .othersites {
    display: none;
  }
  .main .page-footer .othersites {
    display: flex;
  }
}
@media (max-width: 401px) {
  .header .header-content .logo {
    padding: 0 0 0 24px;
  }
  .article-content .ant-list .ant-col {
    flex: 0 0 100%;
    max-width: 100%;
  }
}
